import Vue from 'vue';
import Router from 'vue-router';
import Index from '@/components/Index';
// 引入模块
import cmpMain,{routes as mainRoutes} from "@/components/Main.vue";
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/",
      redirect: "/index/home"
    },
    {
      name: "index",
      path: "/index",
      component: Index,
      children: mainRoutes
    },


  ]
})



