var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"main-content wrapper brown"},[_c('section',[_c('div',{staticClass:"bg1"}),_vm._v(" "),_c('article',[_c('h2',[_vm._v("绘画")]),_vm._v(" "),_c('p',[_vm._v("\n                       绘画在技术层面上，是一个以表面作为支撑面，并在其之上加上颜色的动作。那些表面可以是纸张、油画布、木材、玻璃、漆器或混凝土等，上色的工具可以是画笔，也可以是刀、海绵，甚至是油漆喷雾器、牙刷、手指等等。\n\n                       在艺术用语中，绘画的意义包含了利用此艺术行为再加上图形、构图及其他美学方法去达到表现出绘图者希望表达的概念及意思之目的。\n                       已知最古老的绘画位于法国肖维岩洞（Grotte Chauvet），部分历史学家认为它可以追溯至32000年前[2]。那些画经由红赭石（red ochre）和黑色颜料作雕刻及绘画，主题有马、犀牛、狮子、水牛、猛犸象或是打猎归来的人类[3]。石洞壁画在世界各地均十分常见，例如法国、西班牙、葡萄牙、中国、澳大利亚及印度等。这些绘画的发现对其存在目的引起了众多不同的猜测，有些假设指出那些绘画是当时的人认为绘画可以捕捉动物的灵魂，令他们狩猎更为顺利；一些假设则认为那是泛灵论的一种现象，表达他们对自然的尊敬；亦有另一假设指人类与生俱来已有自我表达的需要，而那些绘画正是其结果；此外亦有那是作为应用资讯的传播的观点[4]。\n                   ")])])]),_vm._v(" "),_c('section',[_c('div',{staticClass:"bg2"}),_vm._v(" "),_c('article',[_c('h2',[_vm._v("雕塑")]),_vm._v(" "),_c('p',[_vm._v("\n                       雕塑是一种造型艺术，以物质材料和手段制作的三维空间形象的视觉艺术。雕塑最早主要是使用雕（通过减除材料来造型）及塑（通过叠加材料来造型）的方式，在石、金属、木、陶瓷等材料来创作，不过现代主义的雕塑，在材料及创造手法上都有很高的自由度，可以利用雕、焊接、模塑或铸造的方式，在各种不同的材质上进行创作。\n\n                       石雕比其他材质易腐坏的雕塑更可以在长时间后保存下来，古文化所留下的雕塑也多半以石雕及陶艺为主。同时期也有很多传统的木雕，但很多都已经腐朽无法保存。古文化的雕塑许多曾都有亮丽的颜色，但现在多半已经褪色[1]。\n\n                       在很多文化中，雕塑是宗教崇拜的中心，一直到最近几世纪之前，大型雕塑都还非常昂贵，非一般个人有能力可能负担，因此大型雕塑多半是宗教或政治的产物。像古地中海、印度、中国、南美及非洲的古文化都留下许多的雕塑。\n\n                       西方文化中的雕塑起源于古希腊，许多希腊的雕塑家在古典希腊时期创作了许多杰出的雕塑品。在中古世纪，哥特式雕塑表现了基督教中的痛苦及激情，文艺复兴时期崇尚古典作品，像米开朗基罗的大卫像就是当时著名的作品[2]。现代主义雕塑较不着重传统的工艺及人体的描绘，改用集合艺术和拾得物作为其作品的呈现。\n                   ")])])]),_vm._v(" "),_c('section',[_c('div',{staticClass:"bg3"}),_vm._v(" "),_c('article',[_c('h2',[_vm._v("建筑")]),_vm._v(" "),_c('p',[_vm._v("\n                       建筑，通常指的是对那些为人类活动提供空间的、或者说拥有内部空间的构造物进行规划、设计、建筑施工而后使用的行为过程的全体或一部分。“建筑”除了可指具体的构造物外，也着重在指创造建造物的行为（过程、技术）等。通常在表示具体建造物时，称之为“建筑物”，但在汉语中两者常被混同使用。建筑经常被人们认为是一种文化的符号，也被当成是一种艺术作品。历史上许多重要的文明都有其独特的代表性建筑成就。\n                       建筑除了包含规划、设计并建造出能反应功能的型式、空间和环境，也需要将技术、社会、自然环境和美学纳入考量。这样的过程需要的是对创意的掌控，以及让材料、科技、光线和阴影能彼此共同合作。此外，建筑也是一个将建筑物和构造物实现的过程，因此需要考量许多实际的层面，包括工程进度、预算估计和施工管理。\n                   ")])])]),_vm._v(" "),_c('section',[_c('div',{staticClass:"bg4"}),_vm._v(" "),_c('article',[_c('h2',[_vm._v("音乐")]),_vm._v(" "),_c('p',[_vm._v("\n                       音乐，广义而言，就是指任何以声音组成的艺术。英文Music一词源于古希腊语的μουσική（mousike），意即缪斯（muse）女神的艺术。而中文的音乐二字，许慎《说文解字》解释为“音，声也。生于心，有节于外，谓之音。”认为音乐和声音的区别，在于音乐需要透过人心去想像和创造。\n\n                       音乐可分为创作、演奏、聆听三个过程，在不同文化和社会，对于音乐的过程及其重要性都有不同的理解，例如在西非鼓乐里，每个人皆是参与者，人们不会区分作曲者、演奏者和聆听者的身份；不过所有的人类文化都有音乐，也就是说，演奏音乐是所有文化当中普遍存在的现象。\n                       因为音乐和数学、物理相关，欧洲自古希腊时代开始即有人论述乐理。在西方乐理中，音乐的主要元素有音高（频率）、节奏和音色。音乐可以分为不同种类，但每种种类的区别常常是含糊和具争议的，而不同种类的音乐会不同程度地强调或忽略某些音乐元素。至于何谓声音，或者乐音和噪音的区别，则没有公认的标准，也因着文化与社会背景而有所不同。许多在当时代具突破性或前卫的音乐被时人批评为“非音乐”，例如贝多芬的大赋格以及早期的爵士乐等等。但近代有不少音乐家不认同传统的理解，例如二十世纪美国作曲家约翰·凯吉认为任何声音和静默皆是音乐。\n                   ")])])]),_vm._v(" "),_c('section',[_c('div',{staticClass:"bg5"}),_vm._v(" "),_c('article',[_c('h2',[_vm._v("文学")]),_vm._v(" "),_c('p',[_vm._v("\n                       文学（英语：literature），在最广泛的意义上，是任何单一的书面作品。更严格地说，文学写作是一种艺术形式，或被认为具有艺术或智力价值的任何单一作品，通常是由于以不同于普通用途的方式部署语言。\n\n                       它的拉丁词根literatura/litteratura（本身起源于littera：letter或handwriting）被用来指代所有的书面记录，尽管当代定义将术语扩展到包括口头或唱歌的文本（口头文学）。文学可以根据是虚构作品还是非虚构作品进行分类，也可以根据是韵文还是散文进行分类；可以根据长篇小说、中篇小说、短篇小说等主要形式进一步区分；作品往往根据历史时期或者遵守某些美学特征或期望（艺术类型）进行分类。\n                   ")])])]),_vm._v(" "),_c('section',[_c('div',{staticClass:"bg6"}),_vm._v(" "),_c('article',[_c('h2',[_vm._v("舞蹈")]),_vm._v(" "),_c('p',[_vm._v("\n                       舞蹈为八大艺术之一[1]，是于三度空间中以身体为语言作“心智交流”现象之人体的运动表达艺术，一般有音乐伴奏，以有节奏的动作为主要表现手段的艺术形式。它一般借助音乐，也借助其他的道具。舞蹈本身有多元的社会意义及作用，包括运动、社交／求偶、祭祀、礼仪等。\n                       在人类文明起源前，舞蹈在仪式、礼仪、庆典和娱乐方面都十分重要。考古提供舞蹈的痕迹，例如9000年前在印度的宾姆贝特卡的岩石壁画和公元前3300年埃及墓壁画描绘的绘画舞蹈人物。\n                       中国在五千年以前就已经出现了舞蹈，[2]产生于奴隶社会[3]，发展到秦汉之际已形成一定特色：长袖细腰、飘逸轻盈、技艺并重、刚柔相济。[3]魏晋南北朝以来中外乐舞的大交流以及唐代帝王的重视，杨贵妃等舞蹈家的出现，使中国表演性舞蹈空前繁荣。[3]\n\n                       宋元明清以后，中国的表演性舞蹈逐渐融入戏曲，成为塑造角色、表达情感、展示剧情的戏曲化舞蹈。\n\n                       中华人民共和国建立后，作为独立的剧场艺术的舞蹈日益完善和成熟起来。[2]\n                     \n                   ")])])]),_vm._v(" "),_c('section',[_c('div',{staticClass:"bg7"}),_vm._v(" "),_c('article',[_c('h2',[_vm._v("戏曲")]),_vm._v(" "),_c('p',[_vm._v("\n                       戏剧 (英语：drama)\n                       是演员将某个故事或情境，以对话、歌唱或动作等方式所表演出来的艺术。戏剧有四个元素，包括了“演员”、“故事（情境）”、“舞台（表演场地）”和“观众”。“演员”是四者当中最重要的元素，他是角色的代言人，必须具备扮演的能力，戏剧与其他艺术类最大的不同之处便在于扮演了，透过演员的扮演，剧本中的角色才得以伸张，如果抛弃了演员的扮演，那么所演出的便不再是戏剧。\n\n                       即使在动画、木偶戏、皮影戏等没有演员现身的戏剧中，演员不过是隐藏在观众的视觉之外的地方演出。\n\n                       戏剧的表演形式多种多样，常见的包括话剧、歌剧、舞剧、音乐剧、木偶戏等。由于文化背景的差别，不同文化所产生戏剧形式往往拥有独特的传统和程式，比如西方戏剧、中国戏曲、印度梵剧、日本能乐、歌舞伎等。\n                   ")])])]),_vm._v(" "),_c('section',[_c('div',{staticClass:"bg8"}),_vm._v(" "),_c('article',[_c('h2',[_vm._v("电影")]),_vm._v(" "),_c('p',[_vm._v("\n                       电影是一种表演艺术、视觉艺术及听觉艺术，利用胶卷、录像带或数字媒体将影像和声音捕捉起来，再加上后期的编辑工作而成。电影中看起来连续的画面，是由一帧帧单独的照片完成的，至于关于电影中运动的感觉，是因为人们因为视觉上的飞现象（Phi\n                       phenomenon），使得对一连串静态图片却会造成移动的错觉。传统对电影中运动感知的理解是因为视觉暂留，使得图像离开后，仍能在眼睛保留“视像”约十分之一秒。因此大脑感觉到图像是“运动”的。但在1916年出版的德国心理学家雨果·明斯特伯格（Hugo\n                       Münsterberg）的《电影：一次心理学研究》中第三章《深度感和运动感》中，雨果·明斯特伯格证明了外观运动绝不是影像滞留（即视觉暂留）的结果，而是（但不仅仅是）对运动的连续阶段的感知。\n                       电影制作本身是艺术也是产业。电影可以由电影摄影机拍摄真实影像再制作而成，也可以利用传统的动画技巧绘制图画再拍摄图画而成，甚至可以利用电脑成像及计算机动画制作电影，也可以在电影中利用上述所有的技术及其他视觉效果。电影技术发展初期有各种不同的放映速度，但现时电影都多以每秒二十四格图像作放映标准[1]。\n                   ")])])])])}]
