<template>
  <!-- 标题图片 -->
  <header class="wrapper">

    <div class="header-bg" :style="headStyle">
      <img class="header-img" src="../../static/art.png" alt="" />
    </div>

    <div class="header-show">
      <div class="slogan">
        <h1>our world</h1>
        <p>Interesting and wonderful</p>
      </div>
      <div class="join"><a href="">join us</a></div>
      <div class="pull">
        <img src="../../static/arrow-down.png" alt="" />
      </div>
    </div>
  </header>
</template>
<script>
export default {
  data() {
    return {
      i: 160,
      headStyle: {},
    };
  },
  methods: {
    moveLeft: function () {
      setTimeout(() => {
        // console.log(this);
        this.i -= 0.4;
        // 刷新位置
        this.headStyle = {
          backgroundPosition: this.i + "px -160px",
        };
        if (this.i > -200) {
          this.moveLeft();
        } else {
          this.moveRight();
        }
      }, 20);
    },
    moveRight: function () {
      setTimeout(() => {
        this.i += 0.4;
        this.headStyle = {
          backgroundPosition: this.i + "px -160px",
        };
        if (this.i < 160) {
          this.moveRight();
        } else {
          this.moveLeft();
        }
      }, 20);
    },
  },
  mounted:function(){
      setTimeout(() => {
        // console.log(this);
        this.i -= 0.4;
        // 刷新位置
        this.headStyle = {
          backgroundPosition: this.i + "px -160px",
        };
        if (this.i > -200) {
          this.moveLeft();
        } else {
          this.moveRight();
        }
      }, 20);
  }
};
</script>


