  },
  {
    path: "letter",
    name: "letter",
    component: letter
  },
  {
    path: "dance",
    name: "dance",
    component: dance
  },
  {
    path: "opera",
    name: "opera",
    component: opera
  },
  {
    path: "film",
    name: "film",
    component: film
  },
  
]
</script>
